body
{
    font-family:"Helvetica";
    background-color:#f5f6fa;
    color: #353b48;
	background-image:url('clouds.jpg');
}

.notification
{
    font-size:24px;
    border:2px solid green;
    padding:12px;
    background-color: rgb(198, 255, 208);
    font-weight:bold;
    color:green;
    margin-bottom:16px;
}

.error
{
    font-size:24px;
    border:2px solid red;
    padding:12px;
    background-color: rgb(255, 208, 208);
    font-weight:bold;
    color:red;
    margin-bottom:16px;
}

#wrapper
{
	border-radius:16px;
    box-shadow:4px 4px 8px;
    width:800px;
    height:100vh;
    margin-left:auto;
    margin-right:auto;
    padding:32px;
    border: 1px hidden #fff;
    background-color:#fff;
}
h1
{
    text-align: center;
    border-bottom: 1px solid #000;
	color:#f55;
    
}
h1, h2
{
    text-transform:uppercase;
}
#filter
{
    font-weight:bold;
    margin-bottom:8px;
}

input
{
    background-color: #f8f9fa;
    border: 1px solid #000;
}

button
{
    text-transform:uppercase;
    font-weight:bold;
    border-radius:8px;
    box-shadow: 2px 2px 5px;
    margin:5px;
}

.deleteButton
{
    color:red;
    box-shadow: 0px 0px 0px;
}

form
{
    border:1px dotted #8a9092;
    border-radius:32px;
    padding:16px;
}